import React, { forwardRef, useRef } from 'react';
import { AriaTextFieldProps, useFocusRing, useTextField } from 'react-aria';
import { mergeProps, useObjectRef } from '@react-aria/utils';
import classNames from 'classnames';

import { Stack } from '../Stack';
import type { DescriptionSeverity } from '../types';
import { Label, Text } from '../Typography';

import styles from './Textarea.module.scss';

export interface TextareaProps
    extends Omit<AriaTextFieldProps, 'errorMessage'> {
    errorMessage?: string;
    descriptionSeverity?: DescriptionSeverity;
    labelSuffix?: string;
    tooltipTrigger?: React.ReactNode;
    rows?: number;
    title?: string;
}

const Textarea = forwardRef<HTMLTextAreaElement, TextareaProps>(
    (props, ref) => {
        const forwardedRef = useObjectRef(ref);
        const fallbackRef = useRef(null);
        const textareaRef = forwardedRef || fallbackRef;

        const {
            labelProps,
            inputProps,
            descriptionProps,
            errorMessageProps,
            validationErrors,
            isInvalid,
        } = useTextField(
            { ...props, inputElementType: 'textarea' },
            textareaRef
        );
        const { isFocusVisible, focusProps } = useFocusRing({
            isTextInput: true,
        });

        const {
            label,
            description,
            errorMessage: customErrorMessage,
            validationBehavior,
            descriptionSeverity = 'regular',
            labelSuffix,
            tooltipTrigger,
            rows,
            title,
        } = props;

        // Allow only native error messages with native validation behaviour
        const errorMessage =
            validationBehavior === 'native'
                ? validationErrors.join(' ')
                : customErrorMessage;

        const hasMessages = errorMessage || description;

        const textareaClassList = classNames(styles.textarea, {
            [styles['textarea--focused']]: isFocusVisible,
            [styles['textarea--invalid']]: isInvalid,
        });

        const descriptionClassList = classNames({
            [styles['messages__description--regular']]:
                descriptionSeverity === 'regular',
            [styles['messages__description--warning']]:
                descriptionSeverity === 'warning',
        });

        return (
            <Stack>
                {label && (
                    <div className={styles.label__container}>
                        <Label
                            weight="600"
                            className={styles.label}
                            {...labelProps}
                        >
                            {label}
                            {labelSuffix && (
                                <>
                                    {/* Use an extra element as separator so everything stays inline and no extra margin on either side needed, which doesn't look right when text starts wrapping */}
                                    <span
                                        className={
                                            styles['label__suffix-separator']
                                        }
                                    ></span>
                                    <Text
                                        variant="line"
                                        italic
                                        size="small"
                                        className={styles.label__suffix}
                                    >
                                        {labelSuffix}
                                    </Text>
                                </>
                            )}
                        </Label>
                        {tooltipTrigger}
                    </div>
                )}

                <textarea
                    {...mergeProps(inputProps, focusProps)}
                    className={textareaClassList}
                    ref={textareaRef}
                    rows={rows}
                    title={title}
                />

                {hasMessages && (
                    <Stack>
                        {/* Show custom error message only when isInvalid */}
                        {isInvalid && errorMessage && (
                            <div
                                {...errorMessageProps}
                                className={styles.messages__error}
                            >
                                <Text variant="line" size="small">
                                    {errorMessage}
                                </Text>
                            </div>
                        )}
                        {description && (
                            <div
                                {...descriptionProps}
                                className={descriptionClassList}
                            >
                                <Text variant="line" size="small">
                                    {description}
                                </Text>
                            </div>
                        )}
                    </Stack>
                )}
            </Stack>
        );
    }
);

Textarea.displayName = 'Textarea';

export default Textarea;
